(function() {
    'use strict';

    angular
        .module('frontEnd')
        .factory('modulesService', modulesService);

    /** @ngInject */
    function modulesService($log, $http) {
        var apiHost = 'http://msa752-001-site1.btempurl.com/api/Bau/Calc';

        var service = {
            index: index,
            show: show,
            calculate: calculate
        };

        return service;

        function index() {
            return [{
                id: "A1-A2",
                icon: "fa-clone",
                label: "A1-A2"
            }, {
                id: "A2-B1",
                icon: "fa-clone",
                label: "A2-B1"
            }, {
                id: "B1",
                icon: "fa-clone",
                label: "B1"
            }, {
                id: "B2",
                icon: "fa-clone",
                label: "B2"
            }];
        }

        function show($params) {
            return $http.get(apiHost + '/' + $params.id)
        }

        function calculate($params) {
            return $http.post(apiHost + '/', $params)
        }
    }
})();

