(function() {
    'use strict';

    angular
        .module('frontEnd')
        .controller('ModuleController', ModuleController);

    /** @ngInject */
    function ModuleController($rootScope, $state, $log, modulesService) {
        var vm = this;
        vm.viewData = {};
        vm.state = $state.params;

        vm.calculatePoints = function($form) {
            $log.info('LOG: ', $form);
        }

        modulesService.show($state.params)
            .success(function($response) {
                vm.viewData.Lessons = $response.PointCount.map(function($item) {
                    $item.label = $item.QuizName.replace(/_/g, ' ');
                    $item.name = $item.QuizName;
                    $item.active = $item.isActive;
                    delete $item.QuizName;
                    return $item;
                });
                /*var tmpData = [];
                Object.keys($response.PointCount)
                    .forEach(function($item) {
                        tmpData.push({
                            label: $item,
                            count: $response.PointCount[$item]
                        });
                    });

                vm.viewData = angular.extend(vm.viewData, { moduleOptions: tmpData });*/

            })
            .error(function($error) {
                $log.error($error);
            })
    }
})();

